body {
  padding: 0;
  margin: 0;
  font-family: Arial, sans-serif;
  color: #fff;
  font-size: 100px;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-grow: 1;
}

.block {
  height: 300px;
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 300px;
  flex-grow: 1;
  flex-shrink: 0;
}

.block-1 {
  background-color: #f00;
}

.block-2 {
  background-color: #c00;
}

.block-3 {
  background-color: #900;
}

.block-4 {
  background-color: #600;
}

.block-5 {
  background-color: #300;
}

.block-6 {
  background-color: #000;
}

@media (min-width:300px) {
  .block-1 {
    order: 1;
  }
  .block-2 {
    order: 2;
  }
  .block-3 {
    order: 3;
  }
  .block-4 {
    order: 4;
  }
  .block-5 {
    order: 5;
  }
  .block-6 {
    order: 6;
  }
}

@media (min-width:600px) {
  .block-3 {
    order: 4;
  }
  .block-4 {
    order: 3;
  }
  .block-5 {
    order: 5;
  }
  .block-6 {
    order: 6;
  }
}

@media (min-width:900px) {
  .block-1 {
    order: 1;
  }
  .block-2 {
    order: 2;
  }
  .block-3 {
    order: 3;
  }
  .block-4 {
    order: 6;
  }
  .block-5 {
    order: 5;
  }
  .block-6 {
    order: 4;
  }
}
